#oberservers-wrapper{
    max-width: 300px;
    min-height: 100px;
    background: coral;
    display: flex;
    justify-content: center;
    margin: auto;
}

.observer-container{
    min-width: 100px;
    min-height: 100px;
    border: 1px solid lightgray;
    line-height: 100px;
    text-align: center;
}

#scoreboard{
    width: 100%;
    background: white;
    text-align: center;
}

#btnIncrementScore{
    background: gray;
    color: white;
    padding: 10px;
    margin: 15px;
    cursor: pointer;
    border-radius: 10px;
}

#scoreBoard-score {
    border: 1px solid rgb(34, 34, 34);
    padding: 10px;
    width: 20px;
    text-align: center;
    margin: auto;
    background: #ff00ff;
    color: #fff;
    font-family: arial;
    font-size: 1.3em;
}